.contacts-user {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 9%;
    width: 100%;
    height: 2px;
    z-index: -1;
    background-color: $basic-green;
  }

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.contacts-user__container {
  padding: 0 20px;
  margin-bottom: 23px;

  @media (min-width: $tablet-width) {
    padding: 0 70px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }

  @include container;
}

.contacts-user__field-container {
  @media (min-width: $tablet-width) {
    margin-bottom: 65px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 78px;
    margin-bottom: 54px;
  }
}

.contacts-user__title {
  margin: 0;
  margin-bottom: 35px;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  color: $black;
  @include title-decore-mobile;

  @media (max-width: $mobile-width-only) {
    margin-right: 79px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 76px;
    font-size: 36px;
    line-height: 36px;

    @include title-decore;
  }
}

.contacts-user__field {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 13px;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 15px;
  }
}

.contacts-user__input {
  &--mail {
    background-image: url('../../img/icon_email.svg');
    background-repeat: no-repeat;
    background-position: center right 3%;

    &:focus {
      background-image: url('../../img/email-green.svg');
    }
    &:invalid {
      background-image: url('../../img/email-red.svg');
    }
  }

  &--phone {
    background-image: url('../../img/icon-phone.svg');
    background-repeat: no-repeat;
    background-position: center right 3%;

    &:focus {
      background-image: url('../../img/phone-green.svg');
      background-repeat: no-repeat;
      background-position: center right 3%;
    }

    &:invalid {
      background-image: url('../../img/phone-red.svg');
    }
  }
}

.contacts-user__description {
  margin-right: 13px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  white-space: nowrap;
  color: $dark-grey;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}
