.button {
  box-sizing: border-box;
  display: block;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Oswald';
  font-size: 16px;
  line-height: 26px;
  text-decoration: none;
  cursor: pointer;
  color: $white;

  &--primary {
    padding: 7px 44px;
    border: none;
    background-color: $basic-green;

    &:hover {
      background-color: #5eaa2f;
    }

    &:active {
      color: #ffffff4d;
      background-color: #5eaa2f;
    }
  }

  &--secondary {
    padding: 10px 0;
    border: none;
    font-size: 16px;
    line-height: 20px;
    background-color: $basic-grey;
    color: $dark-grey;

    &:hover {
      background-color: #ebebeb;
    }

    &:active {
      color: #4444444d;
      background-color: #ebebeb;
    }
  }
}
